import { useState, useEffect } from 'react';
import axios from 'axios';
import '../../styles/Message.css';

const SignOut = () => {

    const _sessionKey = sessionStorage.getItem("SessionKey");
    var [isValid, setIsValid] = useState(false);
  
    var validateSessionKey = async() => {
        try {
            const response = await axios.get("http://localhost:3001/api/checksessionkey?sessionKey=" + _sessionKey);
            setIsValid(response.data);
        } catch (error) {
            alert("Error: " + error);
        }
    }

    var deleteSessionKey = () => {
        try {
            // delete session key from mySQL database
            axios.delete('http://localhost:3001/api/deletesessionkey', { 
                data: { 
                    sessionKey: _sessionKey 
                }
            });
            
            // delete session key from local session storage
            sessionStorage.removeItem('SessionKey');
        } catch (error) {
            console.log("Error while deleting session key: " + error);
        }
    }

    // executes once when page is loaded
    useEffect(() => {
        validateSessionKey();
        deleteSessionKey();
    }, []);

    return (
        <div>
            <p className="loginMessage">Sie wurden erfolgreich abgemeldet.<br/>
                <a href='/signIn'>Zur Anmeldung</a>
            </p>
        </div>
    );
}

export default SignOut;