import { useState } from "react";
import { useHistory } from "react-router-dom";
import Form from "react-bootstrap/Form";
import Button from "react-bootstrap/Button";
import axios from "axios";
import * as Crypto from 'crypto-js';
import '../../styles/Login.css';

// Login Page
export default function SignIn() {
    
    const [username, setUserName] = useState(''); 
    const [password, setPassword] = useState('');
    const [hash, setHash] = useState('');
    let history = useHistory();

    function validateForm() {
        return username.length > 0 && password.length > 5;
    }

    function login(event) {
        event.preventDefault();

        axios.get("http://localhost:3001/api/access?username=" + username + "&password=" + hash)
        .then(res => {
            if (res.data.length > 0) {
              sessionStorage.setItem('SessionKey', res.data);
              history.push("/search"); // redirect to search page
            } else {
              alert("Anmeldung fehlgeschlagen.\nBitte überprüfen Sie Ihren Benutzernamen und Ihr Passwort.")
              sessionStorage.removeItem("SessionKey");
            }
        }).catch(error => {
            console.log("Error during login: " + error);
            alert("Es ist ein Fehler aufgetreten. Details finden Sie im Browserlog.");
        });
    }

    return (
        <div className="Login">
          <Form onSubmit={login}>
            <Form.Group size="lg" controlId="username">
              <Form.Label>Benutzer</Form.Label>
              <Form.Control
                autoFocus
                type="username"
                value={username}
                onChange={(e) => setUserName(e.target.value)}
                placeholder="Benutzername eingeben"
              />
            </Form.Group>
            <Form.Group size="lg" controlId="password">
              <Form.Label>Passwort</Form.Label>
              <Form.Control
                type="password"
                value={password}
                placeholder="Passwort eingeben"
                onChange={(e) => {
                  setPassword(e.target.value);
                  setHash(Crypto.SHA256(e.target.value).toString());
                }}
              />
            </Form.Group>
            <Button block size="lg" type="submit" disabled={!validateForm()}>Anmelden</Button>
            <p>Noch nicht registriert? <a href="/signUp">Registrieren</a><br/></p>
          </Form>
        </div>
    );
}