import {
  BrowserRouter as Router,
  Route,
  Redirect,
  Switch
} from 'react-router-dom';

import Search from './pages/Search/Search';
import Favorites from './pages/Favorites/Favorites';
import SignIn from './pages/SignIn/SignIn';
import SignOut from './pages/SignOut/SignOut';
import SignUp from './pages/SignUp/SignUp';
import NavBar from './components/NavBar/NavBar';


function App() {
  return (
    <Router>
      <NavBar />
      <main>
        <Switch>
          <Route exact path="/search" exact>
            <Search />
          </Route>
          <Route exact path="/favorites" exact>
            <Favorites />
          </Route>
          <Route exact path="/signIn" exact>
            <SignIn />
          </Route>
          <Route exact path="/signOut" exact>
            <SignOut />
          </Route>
          <Route exact path="/signUp" exact>
            <SignUp />
          </Route>
          <Redirect to="/signIn" />
        </Switch>
      </main>
    </Router>
  );
}

export default App;


/*
 Jede Route in der Webanwendung muss hier aufgeführt / bekanntgemacht werden,
 sonst kann man den Routenpfad nicht aufrufen!
*/